import { hasWidthHeight } from "../../../utilities/aria-utils.js";

function getElementByParent(child, parent = "TH") {
  while (child && child?.nodeName !== parent) {
    child = child?.parentElement;
  }
  return child;
}

function isNodeVisible(node, isHeader = true) {
  const SKIP_CLASS = isHeader ? "header-expand" : "el-table__expand-column";
  return (
    hasWidthHeight(node) && ![...(node.classList || [])]?.includes?.(SKIP_CLASS)
  );
}

export { isNodeVisible, getElementByParent };
